import React from 'react';
import formatCurrency  from "../util.js";
import LightSpeed from 'react-reveal/LightSpeed';
import Modal from 'react-modal';
import Zoom from 'react-reveal/Zoom';
class App extends React.Component{
    constructor(props){
        super(props);
        this.state ={
            product: null
        };
    }

    openModal = (product)=>{
        this.setState({product});
    }
    closeModal = ()=>{
        this.setState({product: null});
    }
 
   render(){
       const {product} = this.state;
     return (
       <div className="container-fluid product-list">
           <LightSpeed bottom cascade>
           <ul style={{listStyleType:'none', paddingLeft:"0"}}>
                {this.props.product.map(product => (
                    <li key={product._id}>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-12">
                                    <div className="container-fluid">
                                        <div className="row">
                                            <div className="col-6">
                                            click on Item to view details
                                            <a href={"#"+product._id} onClick={()=>this.openModal(product)}>
                                                <img src={product.image} alt={product.title} className="image-fluid" ></img>
                                            </a>   
                                            </div>
                                            <div className="col-6" style={{opacity: ".8"}}>
                                            <i className="fa fa-star" aria-hidden="true" ></i>{" "}
                                            <b><i>{product.title}</i></b>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="container-fluid">
                                        <div className="row">
                                            <div className="col-6">
                                                <strong><span className="text-danger p-1" style={{fontSize:"1.2em"}}>{formatCurrency(product.price)}</span></strong>  
                                            </div>
                                            <div className="col-6">
                                            <button onClick={()=>this.props.addToCart(product)} className="btn btn-primary" style={{float:"right"}}>Add to Bag</button>    
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                
                            </div>  
                        </div>
                    </li>
                ))}
            </ul>
           </LightSpeed>
            {product && <Modal isOpen={true} onRequestClose={this.closeModal}>
                <Zoom>
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-12">
                                <button onClick={this.closeModal} style={{float: "right"}}>X</button>
                            </div>
                            <div className="col-12">
                                <div className="container-fluid">
                                    <div className="row"> 
                                    <div className="col-sm-6">
                                        <img src={product.image} alt={product.title}></img>
                                    </div>
                                    <div className="col-sm-6">
                                        <p>
                                            <strong>{product.title}</strong>
                                        </p>
                                        <p><b>Description:</b> {" "}
                                           {product.description}
                                        </p>
                                        <p><b>Title:</b> {" "}
                                            {product.title}
                                        </p>
                                        <p> <b>Category:</b> {" "}
                                            {product.category.map(x=>(
                                                <span>{" "} <button className="btn btn-warning">{x}</button></span>
                                            ))}
                                        </p>
                                    </div>
                                    <div className="col-12">
                                        <div className="container-fluid">
                                            <span className="badge badge-danger mb-2 p-2" style={{fontSize:"1.2em"}}>{formatCurrency(product.price)}</span>
                                        </div>
                                        <button onClick={()=>{this.props.addToCart(product); this.closeModal();}} className="btn btn-warning">Add to Cart</button>
                                    </div>
                                    </div>  
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </Zoom>
                </Modal>}
     </div>
     );
     
   } 
}

export default App;
