import React from 'react';
import formatCurrency  from "../util.js";
import Fade from 'react-reveal/Fade';
import { FontAwesomeIcon } from 'react-fontawesome'
class Cart extends React.Component{
    constructor(props){
        super(props);
        this.state ={
            showCheckout: false,
            name: "",
            email:"",
            address: ""
        };
    }
    handleInput = (e)=>{
        this.setState({[e.target.name]: e.target.value })
    }
    creatOrder = (e)=>{
        e.preventDefault();
        const order = {
            name: this.state.name,
            email: this.state.email,
            address: this.state.address,
            cartItems: this.props.cartItems
    };
    this.props.creatOrder(order); 
};  
  render(){
      const {cartItems} = this.props;
    return (
    <div className="container-fluid bag-list">
        <div className="row m-4">
          {cartItems.length===0? <div className=" text-center"  style={{ fontSize: "1.2em",margin:"0 auto"}}>Bag {"("}{cartItems.length}{")"} <br></br><img src="/images/frown.jpg" style={{width:"10em",height:"10em", margin:"0 auto"}}></img>
            <p>Its empty here</p>
            <p>Start shopping to add items to your bag</p>
          </div>:<div className="cart-counter" style={{width: "10%", marginLeft: "80%"}}><i className="fa fa-shopping-cart" aria-hidden="true" style={{fontSize: "3em"}}></i>{" "}<span className="badge badge-danger">{cartItems.length}</span></div>}
        </div>
    <div className="row">
        <Fade left cascade>
        <ul style={{width: '100%'}} className="bag-items">
            {cartItems.map(item =>(
                <li key={item._id} style={{listStyleType: "none"}}>
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-12">
                                <img src={item.image} alt={item.title} />
                            </div>
                            <div className="col-12">
                                <p style={{opacity: ".9"}}><b>{item.title}</b></p>
                                <div>
                                    <b style={{opacity: ".8"}}>{formatCurrency(item.price)} x {item.count}</b>
                                </div>
                                <button className="btn btn-secondary rounded mr-1" onClick={()=>this.props.removeFromCart(item)}>Remove</button>
                                <button className="btn btn-secondary rounded mr-1" onClick={()=>this.props.subFromCart(item)}><b>-</b></button>
                                <button className="btn btn-secondary rounded mr-1" onClick={()=>this.props.addToCart(item)}><b>+</b></button>
                            </div>
                        </div>
                    </div>
                </li>
            ))}
        </ul>
        </Fade>
        
    </div>
    {cartItems.length !==0 && (
        <div className="container-fluid">
        <div className="row" style={{width:"90%", margin:"0 auto"}}>
            <div className="col-12">
                <b className=" badge badge-warning p-2" style={{fontSize: "1.2em", marginLeft: "2.9em"}}>
                Total: {" "}
                {formatCurrency(cartItems.reduce((a,c)=> a + (c.price*c.count), 0))}
                </b>
               
                <button onClick={()=>{this.setState({showCheckout: true})}} className="btn proceed-btn" style={{float:"right"}}>Proceed</button>
            </div> 
            {this.state.showCheckout && (
                <Fade right cascade>
                    <div className="col-12">
                     <form className="mt-5 purchase-form" onSubmit={this.creatOrder}>
                         <Fade right cascade>
                             <p className="text-center"><b>Enter Your Details</b></p>
                         <ul style={{listStyleType:'none'}}>
                            <li>
                                <label>Email:</label>
                                    <input type="email" name="email" required onChange={this.handleInput} />
                            </li>
                            <li>
                                <label>Name:</label>
                                    <input type="text" name="name" required onChange={this.handleInput} />
                            </li>
                            <li>
                                <label>Address:</label>
                                    <input type="text" name="address" required onChange={this.handleInput} />
                            </li>
                            <li>
                                <button type="submit" className="btn btn-primary">Checkout</button>
                            </li>
                        </ul>
                         </Fade>
                     </form>
                 </div> 
                </Fade>
                 
            )}    
        </div>        
        </div>  
    )}
       
    </div>
     
    );
    
  } 
}

export default Cart;

