 import React from 'react';
 import data from './data.json';
 import Products from './components/Products';
 import Filter from './components/Filter';
 import Cart from './components/Cart';
class App extends React.Component{

  constructor(){
    super();
    this.state ={
      products: data.products,
      cartItems: [],    //localStorage.getItem("cartItems")? JSON.parse(localStorage.getItem("cartItems")):[],
      size: "",
      sort: ""
    };
  }

  creatOrder = (order)=>{
    alert("Order for "+ order.name+ " Acknowledged");
  }
  removeFromCart = (product)=>{
    const cartItems = this.state.cartItems.slice();
    this.setState({
      cartItems: cartItems.filter((x)=>x._id !== product._id)
    });
    //localStorage.setItem("cartItems", JSON.stringify(cartItems.filter((x)=>x._id !== product._id)));
  };
  addToCart = (product) =>{
    const cartItems = this.state.cartItems.slice();
    let alreadyInCart = false;
    cartItems.forEach((item)=>{
      if(item._id===product._id){
        item.count++;
        alreadyInCart = true;
      }
    });
    if(!alreadyInCart){
      cartItems.push({...product, count: 1}); 
    }
    this.setState({cartItems});
    //localStorage.setItem("cartItems", JSON.stringify(cartItems.filter((x)=>x._id !== product._id)));
  };
  subFromCart = (product) =>{
    const cartItems = this.state.cartItems.slice();
    cartItems.forEach((item)=>{
      if(item._id===product._id && item.count>=1){
        item.count--;
      }
    });
    this.setState({cartItems});
    //localStorage.setItem("cartItems", JSON.stringify(cartItems.filter((x)=>x._id !== product._id)));
  };
  sortProducts =(event) =>{
    const sort = event.target.value;
    this.setState((state) =>({
      sort: sort,
      products: this.state.products
      .slice()
      .sort((a,b) =>
        sort === "lowest"
        ? a.price > b.price
        ? 1
        :-1
        : sort === "highest"
        ? a.price < b.price
        ? 1
        :-1
        : a._id < b._id
        ? 1
        :-1
       ),
    }));
  };
  filterProducts=(event)=>{
    if(event.target.value===""){
      this.setState({size:event.target.value, products: data.products});
    }else{
      this.setState({
        size: event.target.value,
        products: data.products.filter((product)=>product.category.indexOf(event.target.value)>=0),
      });
    }
    
  }
    render(){
      return (
        <div className="container-fluid">
          <header className="row bg-primary text-center p-5" style={{fontWeight: "bold", color:"yellow", fontSize:"1.2em"}}>
           <p style={{width: "80%", textAlign: "center", margin: "0 auto"}}> SOKO STORE FRONT </p>
          </header>
        <div className="row main-body-container">
        
       <main className="col-12">
         <div className="container-fluid">
            <div className="row">
              <div className="col-md-6">
                <Filter 
                count={this.state.products.length} 
                size={this.state.size}
                sort={this.state.sort}
                filterProducts={this.filterProducts}
                sortProducts={this.sortProducts}
                />
                <Products product={this.state.products} addToCart={this.addToCart} />
              </div>
              <div className="col-md-6">
                <Cart cartItems={this.state.cartItems} subFromCart={this.subFromCart} addToCart={this.addToCart} removeFromCart={this.removeFromCart} creatOrder={this.creatOrder} />
              </div>
            </div>
         </div>
        </main>
        <footer className="col-12 text-center bg-secondary p-3 mt-3">
          &copy;Gamaliel's Technologies All rights reserved
        </footer>
        </div>
      </div>
      );
      
    } 
}

export default App;
