import React from 'react';
 

class App extends React.Component{
 
   render(){
     return (
       <div className="container-fluid m-3 text-center">
            <div className="row">
              <div className="col-6" style={{opacity:'.7'}}><b><span className="badge badge-secondary">{this.props.count}</span> {" "} Items Available</b> </div>
              {/* <div className="col-4">{" "}
              
              <select value={this.props.sort} onChange={this.props.sortProducts}>
              <option value="latest">Latest</option >
              <option value="Lowest">Lowest</option >
              <option value="Highest">Highest</option >
              </select>
              
              </div> */}
              <div className="col-6" >
                <span style={{opacity:'.7', fontWeight:"bold"}}>Filter {" "}</span>
              <select value={this.props.size} onChange={this.props.filterProducts}>
                <option value="all">All</option >
                <option value="women">Women</option >
                <option value="men">Men</option >
                <option value="gowns">Gowns</option >
                <option value="caps">Caps</option >
                <option value="trousers">Trousers</option >
                <option value="shirts">Shirts</option >
                <option value="shoes">Shoes</option>
              </select>
                
              </div>
            </div>
        </div>
     );
     
   } 
}

export default App;
